/** @jsxImportSource theme-ui */
import * as React from 'react'
import {useState, ChangeEvent} from 'react'
import {useTranslation} from 'react-i18next'

interface IDonationSinglePriceBlock {
  amount: number | null
  setAmount: (amount: number) => void
  priceTag: string
}

const DonationSinglePriceBlock: React.FC<IDonationSinglePriceBlock> = ({
  amount,
  setAmount,
  priceTag,
}) => {
  const {t} = useTranslation()
  const [customAmount, setCustomAmount] = useState<string>(
    amount?.toString() || '',
  )
  const handleChangeAmount = (event: ChangeEvent<HTMLInputElement>) => {
    const value = event.currentTarget.value
    setCustomAmount(value)
    setAmount(parseInt(value))
  }

  return (
    <div sx={{gridColumnEnd: 'span 2'}}>
      <input
        type="number"
        sx={{
          width: '100%',
          height: '100%',
          border: '1px solid #DEE3E3',
          borderRadius: 4,
          padding: '15px 12px',
          fontSize: 13,
          outline: 0,
          '::placeholder': {
            color: '#717A7B',
          },
          '&:focus': {
            border: '1px solid #055064',
          },
        }}
        placeholder={t('Amount') + ' ' + priceTag}
        value={customAmount}
        onChange={handleChangeAmount}
      />
    </div>
  )
}

export default DonationSinglePriceBlock
