/** @jsxImportSource theme-ui */
import SelectedIcon from 'icons/Selected'
import {useEffect, useMemo} from 'react'
import * as React from 'react'
import {
  DISABLE_ONE_TIME_FOR,
  DISABLE_MONTHLY_FOR,
  DonationPurpose,
  DonationType,
  RamadanBasketDonationType,
  RamadanIftarDonationType,
  WaterForAllDonationType,
  UdhiyaDonationType,
  AllDonationType,
  AtlasBasketDonationType,
} from './shared'
import {useTranslation} from 'react-i18next'

const donationTypeArray = Object.keys(DonationType)
const waterForAllDonationTypeArray = Object.keys(WaterForAllDonationType)
const udhiyaDonationTypeArray = Object.keys(UdhiyaDonationType)
const ramadanBasketDonationTypeArray = Object.keys(RamadanBasketDonationType)
const ramadanIftarDonationTypeArray = Object.keys(RamadanIftarDonationType)
const atlasBasketDonationTypeArray = Object.keys(AtlasBasketDonationType)

const getDonationTypes = (donationPurpose: DonationPurpose): string[] => {
  let modifiedDonationTypeArray = donationTypeArray.slice()
  if (DISABLE_ONE_TIME_FOR.includes(donationPurpose)) {
    modifiedDonationTypeArray = modifiedDonationTypeArray.filter(
      (donationType) => donationType !== DonationType['One Time'],
    )
  }
  if (DISABLE_MONTHLY_FOR.includes(donationPurpose)) {
    modifiedDonationTypeArray = modifiedDonationTypeArray.filter(
      (donationType) => donationType !== DonationType.Monthly,
    )
  }
  switch (donationPurpose) {
    case DonationPurpose['Water For All']:
      modifiedDonationTypeArray = modifiedDonationTypeArray.concat(
        waterForAllDonationTypeArray,
      )
      break
    // @ts-ignore We don't care, it is only because we disabled it
    case DonationPurpose.Udhiya:
      modifiedDonationTypeArray = modifiedDonationTypeArray.concat(
        udhiyaDonationTypeArray,
      )
      break
    // @ts-ignore We don't care, it is only because we disabled it
    case DonationPurpose['Ramadan Basket']:
      modifiedDonationTypeArray = modifiedDonationTypeArray.concat(
        ramadanBasketDonationTypeArray,
      )
      break
    case DonationPurpose['Ramadan Iftar']:
      modifiedDonationTypeArray = modifiedDonationTypeArray.concat(
        ramadanIftarDonationTypeArray,
      )
      break
    case DonationPurpose['Atlas Relief']:
      modifiedDonationTypeArray = modifiedDonationTypeArray.concat(
        atlasBasketDonationTypeArray,
      )
      break
  }
  return modifiedDonationTypeArray
}

interface IDonationTypeBlock {
  donationPurpose: DonationPurpose
  donationType: AllDonationType
  setDonationType: (donationType: AllDonationType) => void
}

const customDonationTypeNames: {[key: string]: {[key: string]: string}} = {
  'Atlas Relief': {
    'One Time': 'Atlas Amount',
  },
}

const DonationTypeBlock: React.FC<IDonationTypeBlock> = ({
  donationPurpose,
  donationType,
  setDonationType,
}) => {
  const {t} = useTranslation()
  const donationTypes = useMemo(
    () => getDonationTypes(donationPurpose),
    [donationPurpose],
  )
  useEffect(() => {
    if (donationPurpose === DonationPurpose['Atlas Relief']) {
      setDonationType(AllDonationType['Atlas Basket'])
    } else if (donationPurpose === DonationPurpose['Water For All']) {
      setDonationType(AllDonationType['Build Your Own Well'])
    } else if (donationPurpose === DonationPurpose['Ansar']) {
      setDonationType(AllDonationType['Monthly'])
    } else if (!donationTypes.includes(donationType)) {
      setDonationType(donationTypes[0] as DonationType)
    }
  }, [donationPurpose]) // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <div
      sx={{
        display: 'flex',
      }}
    >
      {donationTypes.length > 1 &&
        donationTypes.map((name, index, arr) => {
          const isSelected = donationType === name
          const handleClick = () => {
            if (!isSelected) setDonationType(name as DonationType)
          }
          return (
            <button
              key={name}
              onClick={handleClick}
              type="button"
              sx={{
                flex: donationTypes.length > 2 ? 'auto' : 1,
                fontSize: donationTypes.length > 2 ? 13 : 14,
                cursor: 'pointer',
                height: [44, , 38],
                borderTopRightRadius: index === arr.length - 1 ? 4 : 0,
                borderTopLeftRadius: index === 0 ? 4 : 0,
                borderColor: 'primary',
                borderLeftColor: index === 0 ? 'primary' : 'transparent',
                borderWidth: 1,
                backgroundColor: isSelected ? 'primary' : 'transparent',
                color: isSelected ? '#fff' : 'primary',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
              }}
            >
              <SelectedIcon
                style={{
                  display: isSelected ? 'inline' : 'none',
                  marginRight: 6,
                }}
              />
              {t(customDonationTypeNames?.[donationPurpose]?.[name] || name)}
            </button>
          )
        })}
    </div>
  )
}

export default DonationTypeBlock
