/** @jsxImportSource theme-ui */
import BackArrow from 'icons/BackArrow'
import {Dispatch, SetStateAction} from 'react'
import * as React from 'react'
import NextButton from './NextButton'
import PaymentFormPaypal from './PaymentFormPayPal'
import PaymentFormStripe from './PaymentFormStripe'
import {PaymentMethod, paymentMethods} from './shared'
import {useTranslation} from 'react-i18next'
import theme from 'styles/theme'

interface IPaymentFormProps {
  amountString: string
  donationPurpose: string
  handleBackClick: () => void
  isLoading: boolean
  payerName: string
  setPayerName: (payerName: string) => void
  paymentMethod: PaymentMethod['methodName']
  setPaymentMethod: (paymentMethod: PaymentMethod['methodName']) => void
  setStripeElementsComplete: Dispatch<
    SetStateAction<{
      cardNumber: boolean
      cardExpiry: boolean
      cardCvc: boolean
    }>
  >
}

const PaymentForm: React.FC<IPaymentFormProps> = ({
  amountString,
  donationPurpose,
  handleBackClick,
  isLoading,
  setPayerName,
  payerName,
  setStripeElementsComplete,
  paymentMethod,
  setPaymentMethod,
}) => {
  const {t} = useTranslation()
  return (
    <div>
      <button
        type="button"
        onClick={handleBackClick}
        sx={{
          display: 'flex',
          alignItems: 'center',
          backgroundColor: 'transparent',
          border: 'none',
          color: '#A7A7A7',
          fontSize: 16,
          cursor: 'pointer',
        }}
      >
        <BackArrow />
        <span sx={{ml: 12}}>{t('Back')}</span>
      </button>
      <h3
        sx={{
          fontSize: 16,
          fontWeight: 400,
          color: '#333',
          //   mt: 35,
          mb: 9,
        }}
      >
        {amountString}
      </h3>
      <div sx={{color: '#717A7B', fontSize: 14}}>
        {t('For')} {t(donationPurpose)}
      </div>
      <div
        sx={{
          mt: 24,
          backgroundColor: '#F5F5F5',
          borderRadius: 4,
        }}
      >
        <div
          sx={{
            width: '100%',
            display: 'flex',
          }}
        >
          {paymentMethods.map(({Icon, label, methodName}, index) => {
            const isSelected = paymentMethod === methodName
            const handleClick = () => {
              if (!isSelected) setPaymentMethod(methodName)
            }
            let iconColor = 'white'
            if (!isSelected && typeof theme.colors?.primary === 'string') {
              iconColor = theme.colors?.primary
            }
            return (
              <button
                key={methodName}
                onClick={handleClick}
                type="button"
                sx={{
                  flex: 1,
                  fontSize: 14,
                  cursor: 'pointer',
                  height: 38,
                  borderTopRightRadius:
                    index === paymentMethods.length - 1 ? 4 : 0,
                  borderTopLeftRadius: index === 0 ? 4 : 0,
                  borderColor: 'primary',
                  borderLeftColor: index === 0 ? 'primary' : 'transparent',
                  borderWidth: 1,
                  backgroundColor: isSelected ? 'primary' : 'transparent',
                  color: isSelected ? '#fff' : 'primary',
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                }}
              >
                <Icon color={iconColor} />
                {t(label)}
              </button>
            )
          })}
        </div>
        {paymentMethod == 'stripe' && (
          <PaymentFormStripe
            setPayerName={setPayerName}
            payerName={payerName}
            setStripeElementsComplete={setStripeElementsComplete}
          />
        )}
        {paymentMethod == 'payPal' && <PaymentFormPaypal />}
      </div>
      <NextButton type="submit" sx={{mt: 27}} disabled={isLoading} />
    </div>
  )
}

export default PaymentForm
