/** @jsxImportSource theme-ui */
import * as React from 'react'
import {ChangeEvent, useEffect, useMemo, useState} from 'react'
import {useTranslation} from 'react-i18next'
import {ValueType} from 'react-select'

import CustomSelect, {ISelectOption} from './CustomSelect'
import {UdhiyaMetadata} from './shared'

const buildYourOwnWellList: UdhiyaMetadata[] = [
  {
    type: 'Udhiya',
    country: 'Morocco',
    unit_price: 185,
    quantity: 1,
    sacrifice: 'Sheep',
  },
  {
    type: 'Udhiya',
    country: 'Ivory Coast',
    unit_price: 195,
    quantity: 1,
    sacrifice: 'Sheep',
  },
  {
    type: 'Udhiya',
    country: 'Nepal',
    unit_price: 65,
    quantity: 1,
    sacrifice: 'Goat',
  },
]

interface IUdhiya {
  setMetadata: (metadata: UdhiyaMetadata) => void
  setAmount: (metadata: number) => void
}

const Udhiya: React.FC<IUdhiya> = ({setMetadata, setAmount}) => {
  const {t} = useTranslation()
  const [name, setName] = useState<string>('')
  const [itemIndex, setItemIndex] = useState<number>(0)

  const handleChangeType: (value: ValueType<ISelectOption, false>) => void = (
    item,
  ) => {
    if (item) setItemIndex(parseInt(item.value))
  }

  const handleChangeInput = (event: ChangeEvent<HTMLInputElement>) => {
    setName(event.currentTarget.value)
  }

  const selectUdhiyaList = useMemo(
    () =>
      buildYourOwnWellList.map((item, index) => ({
        value: `${index}`,
        label: (
          <div>
            {item.type} - {t(item.country)} - {t(item.sacrifice)} -{' '}
            <strong>${item.unit_price} USD </strong>
            <div
              sx={{
                fontSize: 12,
              }}
            ></div>
          </div>
        ),
      })),
    [t],
  )

  useEffect(() => {
    const metadata: UdhiyaMetadata = buildYourOwnWellList[itemIndex]
    setMetadata({...metadata, name})
    setAmount(metadata.unit_price)
  }, [itemIndex, name, setMetadata, setAmount])

  return (
    <div
      sx={{
        py: 16,
        px: 13,
      }}
    >
      <CustomSelect
        value={selectUdhiyaList[itemIndex]}
        onChange={handleChangeType}
        options={selectUdhiyaList}
      />
      <input
        type="text"
        sx={{
          mt: 14,
          width: '100%',
          height: '100%',
          border: '1px solid #DEE3E3',
          borderRadius: 4,
          padding: '15px 12px',
          fontSize: 13,
          outline: 0,
          '::placeholder': {
            color: '#717A7B',
          },
          '&:focus': {
            border: '1px solid #055064',
          },
        }}
        required
        placeholder={
          t('Full name for whom the udhiya will be made') ||
          'Full name for whom the udhiya will be made'
        }
        name="name"
        value={name}
        onChange={handleChangeInput}
      />
    </div>
  )
}

export default Udhiya
