/** @jsxImportSource theme-ui */
import {ChangeEvent, useEffect} from 'react'
import * as React from 'react'
import {FamilyBasketMetadata, Metadata} from './shared'
import {useTranslation} from 'react-i18next'

interface IFamilyBasket {
  amount: number | null
  setAmount: (amount: number) => void
  metadata: Metadata | null
  setMetadata: (metadata: FamilyBasketMetadata) => void
  unit_price: number
  text: string
}

const buildMetadata = ({
  quantity = 1,
  unit_price = 70,
}: {
  quantity?: number
  unit_price?: number
}): FamilyBasketMetadata => {
  return {
    type: 'Family basket',
    country: 'Morocco',
    unit_price,
    quantity,
  }
}

const FamilyBasket: React.FC<IFamilyBasket> = ({
  amount,
  setAmount,
  metadata,
  setMetadata,
  text,
  unit_price,
}) => {
  const {t} = useTranslation()
  useEffect(() => {
    if (!metadata || metadata.type !== 'Build your own well') {
      setMetadata(buildMetadata({quantity: 1, unit_price}))
    }
  }, []) // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    if (metadata && amount !== metadata.unit_price * metadata.quantity) {
      setAmount(metadata.unit_price * metadata.quantity)
    }
  }, [metadata]) // eslint-disable-line react-hooks/exhaustive-deps

  const handleChangeQuantity = (event: ChangeEvent<HTMLInputElement>) => {
    const value = event.currentTarget.value
    const quantity = parseInt(value, 10) || 0
    setMetadata(
      buildMetadata({
        quantity,
        unit_price,
      }),
    )
  }
  const displayedQuantity = metadata ? metadata.quantity + '' : ''
  return (
    <div
      sx={{
        py: 16,
        px: 13,
      }}
    >
      <div
        sx={{
          bg: '#fff',
          borderRadius: 4,
          height: 46,
          display: 'flex',
          alignItems: 'center',
          px: 12,
          boxShadow: '0px 1px 2px rgba(0, 0, 0, 0.07)',
          cursor: 'not-allowed',
        }}
      >
        {text} - {unit_price} USD
      </div>
      {/* <CustomSelect /> */}
      <div
        sx={{
          display: 'flex',
          mt: 14,
          justifyContent: 'space-between',
        }}
      >
        <div
          sx={{
            width: [140, , 152],
            height: 46,
            position: 'relative',
          }}
        >
          <input
            type="number"
            sx={{
              width: '100%',
              height: '100%',
              border: '1px solid #DEE3E3',
              borderRadius: 4,
              px: 15,
              pl: 72,
              fontSize: 13,
              outline: 0,
              '::placeholder': {
                color: '#717A7B',
              },
              '&:focus': {
                border: '1px solid #055064',
              },
            }}
            defaultValue={displayedQuantity || 1}
            onChange={handleChangeQuantity}
          />
          <span
            sx={{
              color: '#949FA0',
              fontSize: 13,
              position: 'absolute',
              left: 12,
              top: '50%',
              transform: 'translateY(-50%)',
            }}
          >
            {t('Quantity')}:
          </span>
        </div>
        <div
          sx={{
            width: [140, , 152],
            bg: '#fff',
            borderRadius: 4,
            height: 46,
            display: 'flex',
            alignItems: 'center',
            px: 12,
            border: '1px solid #DEE3E3',
            cursor: 'not-allowed',
          }}
        >
          <span sx={{color: '#949FA0', fontSize: 13}}>{t('Total')}:</span>
          <span>&nbsp;{amount} USD</span>
        </div>
      </div>
    </div>
  )
}

export default FamilyBasket
