/** @jsxImportSource theme-ui */
import * as React from 'react'
import Select, {
  ActionMeta,
  OptionsType,
  StylesConfig,
  Theme,
  ValueType,
} from 'react-select'

export interface ISelectOption {
  value: string
  label: string | React.ReactElement
}

type Props = {
  options: OptionsType<ISelectOption>
  onChange: (
    value: ValueType<ISelectOption, false>,
    actionMeta: ActionMeta<ISelectOption>,
  ) => void
  value?: ISelectOption
  // style?: StyleP
}

const customSelectTheme = (theme: Theme): Theme => {
  return {
    ...theme,
    colors: {
      ...theme.colors,
      primary: '#055064',
      primary75: '#0A343FAA',
      primary25: '#0A343F33',
      primary50: '#0A343F77',
    },
  }
}

const customSelectStyles: StylesConfig<ISelectOption, false> = {
  indicatorSeparator: () => {
    return {display: 'none'}
  },
  dropdownIndicator: (provided) => {
    return {...provided, color: '#000'}
  },
  option: (provided, state) => ({
    ...provided,
    color: state.isDisabled ? '#055064' : (provided.color as string),
    opacity: state.isDisabled ? 0.6 : 1,
  }),
}

const CustomSelect: React.FC<Props> = (props) => {
  return (
    <Select
      styles={customSelectStyles}
      theme={customSelectTheme}
      isMulti={false}
      {...props}
      // FIXME: custom style Does this work??
      style={{
        fontSize: 14,
        color: 'primary',
        borderRadius: 4,
        // ...props.style,
      }}
    />
  )
}

export default CustomSelect
