/** @jsxImportSource theme-ui */
import * as Sentry from '@sentry/browser'
import './i18n'
import {Elements} from '@stripe/react-stripe-js'
import {loadStripe} from '@stripe/stripe-js'
import * as React from 'react'
import {GoogleReCaptchaProvider} from 'react-google-recaptcha-v3'
import {toast} from 'react-toastify'
import 'react-toastify/dist/ReactToastify.min.css'
import {ThemeProvider} from 'theme-ui'
import 'url-search-params-polyfill'
import DonationBox from 'components/DonationBox'
import env from 'lib/env'
import theme from 'styles/theme'
import donationBg from 'images/donation-bg.png'

const SENTRY_DSN = env.REACT_APP_SENTRY_DSN
const STRIPE_PUB_KEY = env.REACT_APP_STRIPE_PUB_KEY
const REACT_APP_GOOGLE_RECAPTCHA_KEY = env.REACT_APP_GOOGLE_RECAPTCHA_KEY
const BRANCH = env.VERCEL_GITHUB_COMMIT_REF

if (SENTRY_DSN) {
  const sentryEnvironment = BRANCH === 'master' ? 'production' : 'staging'
  Sentry.init({dsn: SENTRY_DSN, environment: sentryEnvironment})
}

const stripePromise = loadStripe(STRIPE_PUB_KEY)

toast.configure({
  position: 'top-center',
})

const App: React.FC = () => {
  return (
    <ThemeProvider theme={theme}>
      <GoogleReCaptchaProvider reCaptchaKey={REACT_APP_GOOGLE_RECAPTCHA_KEY}>
        <Elements stripe={stripePromise}>
          <div
            id="white-background"
            sx={{
              backgroundColor: '#fff',
              position: 'absolute',
              top: 0,
              height: 378,
              right: 0,
              left: 0,
            }}
          ></div>
          <div
            sx={{
              width: [, , 1028],
              mx: 'auto',
              maxWidth: [, , '90%'],
              position: 'relative',
              // background: 'red',
              zIndex: 10,
              // backgroundImage: `url(${donationBg})`,
              backgroundImage: `url('https://ummaty.org/static/87af0b2ae306849e37c3e3579d39f081/d5ada/donation-bg.webp')`,
              backgroundRepeat: 'no-repeat',
              backgroundSize: 'cover',
              borderRadius: [, , 25],
              padding: [, , '37px 40px'],
              minHeight: [, , 658],
              display: 'flex',
              flexDirection: 'row-reverse',
              '*': {
                boxSizing: 'border-box',
              },
            }}
          >
            {/* <img
              src={donationBg}
              alt=""
              sx={{
                position: 'absolute',
                top: 0,
                right: 0,
                left: 0,
                bottom: 0,
                width: '100%',
                height: '100%',
                zIndex: 1,
              }}
            /> */}
            <DonationBox />
          </div>
        </Elements>
      </GoogleReCaptchaProvider>
    </ThemeProvider>
  )
}

export default App
