/** @jsxImportSource theme-ui */
import {Fragment, useState, FormEvent, useEffect} from 'react'
import {toast} from 'react-toastify'
import * as React from 'react'
import {useGoogleReCaptcha} from 'react-google-recaptcha-v3'
import PaymentForm from './PaymentForm'
import DonationFooter from './DonationFooter'
import DonationMultiPriceBlock from './DonationMultiPriceBlock'
import DonationPurposeBlock from './DonationPurposeBlock'
import DonationSinglePriceBlock from './DonationSinglePriceBlock'
import DonationTypeBlock from './DonationTypeBlock'
import DonationUserInfo from './DonationUserInfo'
import {
  DISABLE_AMOUNTS_FOR,
  DonationPurpose,
  DonationType,
  getAmounts,
  UserInfo,
  AllDonationType,
  WaterForAllDonationType,
  PaymentMethod,
  Metadata,
  RamadanBasketDonationType,
  RamadanIftarDonationType,
  AtlasBasketDonationType,
  UdhiyaDonationType,
  MINIMAL_DONATION_AMOUNT,
  DonationPurposeSlugs,
  DISABLED_DONATION_PURPOSES,
} from './shared'
import useStripePayment from 'hooks/useStripePayment'
import BuildYourOwnWell from './BuildYourOwnWell'
import usePayPalPayment from 'hooks/usePayPalPayment'
import FixedLoader from 'components/FixedLoader'
import ThankYouBox from './ThankYouBox'
import ErrorBox from './ErrorBox'
import FamilyBasket from './FamilyBasket'
import {useTranslation} from 'react-i18next'
import {Locale} from 'i18n'
import Udhiya from './Udhiya'

type PageNumbers = 0 | 1 | 2 | 3

interface IDonationBox {}

const DonationBox: React.FC<IDonationBox> = () => {
  const {t, i18n} = useTranslation()
  const {executeRecaptcha} = useGoogleReCaptcha()
  const language = i18n.language as Locale
  const [pageNumber, setPageNumber] = useState<PageNumbers>(0)
  const [donationPurpose, setDonationPurpose] = useState<DonationPurpose>(
    DonationPurpose['General Donation'],
  )

  const [donationType, setDonationType] = useState<AllDonationType>(
    DonationType['One Time'],
  )
  const [amount, setAmount] = useState<number | null>(null)
  const [option, setOption] = useState<number>(0)
  const [metadata, setMetadata] = useState<Metadata | null>(null)
  const handleChangeDonationPurpose = (newDonationPurpose: DonationPurpose) => {
    setAmount(null)
    setMetadata(null)
    setDonationPurpose(newDonationPurpose)
  }
  const [userInfo, setUserInfo] = useState<UserInfo>({
    firstName: '',
    lastName: '',
    email: '',
  })
  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search)
    const to = urlParams.get('to') || ''
    if (
      to &&
      DonationPurposeSlugs[to] &&
      !DISABLED_DONATION_PURPOSES.includes(DonationPurposeSlugs[to])
    ) {
      handleChangeDonationPurpose(DonationPurposeSlugs[to])
    }
  }, [])
  useEffect(() => {
    if (metadata !== null && donationType in DonationType) {
      // We are in a normal donation type, we don't need metadata
      setMetadata(null)
    }
  }, [donationType]) // eslint-disable-line react-hooks/exhaustive-deps
  const handlePaymentCompleted = (success: boolean) => {
    if (success) setPageNumber(2)
    else setPageNumber(3)
  }

  const showAmounts = !DISABLE_AMOUNTS_FOR.includes(donationPurpose)
  const isMonthlyDonation = donationType === DonationType.Monthly
  const amounts: number[] =
    donationType in AllDonationType ? getAmounts(donationType) : []
  const amountString = `${t('Donating')} ${amount} USD${
    isMonthlyDonation ? '/mo' : ''
  }`
  const [paymentMethod, setPaymentMethod] =
    useState<PaymentMethod['methodName']>('stripe')
  const [payerName, setPayerName] = useState<string>('')
  const {initiatePayment: initiateStripePayment, loading: stripeLoading} =
    useStripePayment()
  const {initiatePayment: initiatePayPalPayment, loading: payPalLoading} =
    usePayPalPayment({
      onPaymentCompleted: handlePaymentCompleted,
    })
  const [areStripeElementsComplete, setStripeElementsComplete] = useState({
    cardNumber: false,
    cardExpiry: false,
    cardCvc: false,
  })
  const handleSubmit = async (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault()
    const recaptcha = (await executeRecaptcha?.('homepage')) || ''
    const {firstName, lastName, email} = userInfo
    if (!amount || amount < MINIMAL_DONATION_AMOUNT) {
      toast.warn(
        `You need to add an amount of at least $${MINIMAL_DONATION_AMOUNT} USD`,
      )
      return
    }
    // if (!email || !/\S+@\S+\.\S+/.test(email)) {
    //   toast.warn('You need to enter a valid email')
    //   setPageNumber(0)
    //   return
    // }
    // if (!firstName || !lastName) {
    //   toast.warn('You need to enter your first name and your last name')
    //   setPageNumber(0)
    //   return
    // }

    if (pageNumber === 0) {
      setPageNumber(1)
    } else {
      if (paymentMethod === 'stripe') {
        let valid = true
        Object.entries(areStripeElementsComplete).forEach(
          ([element, complete]) => {
            if (!complete) {
              // https://stackoverflow.com/a/44280814/1673761
              const elementName = element
                .split(/(?=[A-Z])/)
                .join(' ')
                .toLowerCase()
              toast.error(`Your ${elementName} is incomplete`)
              valid = false
            }
          },
        )
        if (!valid) return
        const {message, success} = await initiateStripePayment({
          amount,
          firstName,
          lastName,
          email,
          donationPurpose,
          payerName,
          metadata,
          isRecurrent: donationType === DonationType.Monthly,
          language,
          recaptcha,
        })
        handlePaymentCompleted(success)
        return
      } else if (paymentMethod === 'payPal') {
        initiatePayPalPayment({
          amount,
          firstName,
          lastName,
          email,
          donationPurpose,
          metadata,
          isRecurrent: donationType === DonationType.Monthly,
          language,
          recaptcha,
        })
      } else {
        // never
        throw new Error('This payment method is not handled')
      }
    }
  }
  const handleBackClick = () => {
    setPageNumber(0)
  }
  const isNormalDonation = donationType in DonationType
  return (
    <Fragment>
      <form
        onSubmit={handleSubmit}
        sx={{
          zIndex: 10,
          p: [28, , 24],
          backgroundColor: '#fff',
          borderRadius: [, , 19],
          width: ['100%', 387],
          display: 'flex',
          flexDirection: 'column',
        }}
      >
        {pageNumber === 0 && (
          <Fragment>
            <DonationPurposeBlock
              donationPurpose={donationPurpose}
              setDonationPurpose={handleChangeDonationPurpose}
            />
            <div
              sx={{
                mt: 24,
                backgroundColor: '#F5F5F5',
                borderRadius: 4,
              }}
            >
              <DonationTypeBlock
                donationPurpose={donationPurpose}
                donationType={donationType}
                setDonationType={setDonationType}
              />
              {isNormalDonation && showAmounts && (
                <DonationMultiPriceBlock
                  key={donationType + donationPurpose}
                  amount={amount}
                  setAmount={setAmount}
                  defaultAmount={amounts[1]}
                  priceTag={isMonthlyDonation ? 'USD/mo' : 'USD'}
                  amounts={amounts}
                />
              )}
              {donationType ===
                WaterForAllDonationType['Build Your Own Well'] && (
                <BuildYourOwnWell
                  option={option}
                  setOption={setOption}
                  setAmount={setAmount}
                  metadata={metadata}
                  setMetadata={setMetadata}
                />
              )}
              {donationType === UdhiyaDonationType.Udhiya && (
                <Udhiya setAmount={setAmount} setMetadata={setMetadata} />
              )}
              {donationType === RamadanBasketDonationType['Family Basket'] && (
                <FamilyBasket
                  amount={amount}
                  setAmount={setAmount}
                  metadata={metadata}
                  setMetadata={setMetadata}
                  unit_price={70}
                  text={t('Morocco')}
                />
              )}
              {donationType === RamadanIftarDonationType['Iftar Meal'] && (
                <FamilyBasket
                  amount={amount}
                  setAmount={setAmount}
                  metadata={metadata}
                  setMetadata={setMetadata}
                  unit_price={3}
                  text={t('Iftar Meal')}
                />
              )}
              {donationType === AtlasBasketDonationType['Atlas Basket'] && (
                <FamilyBasket
                  amount={amount}
                  setAmount={setAmount}
                  metadata={metadata}
                  setMetadata={setMetadata}
                  unit_price={100}
                  text={t('Sponsor a family this winter')}
                />
              )}
            </div>
            {isNormalDonation && !showAmounts && (
              <DonationSinglePriceBlock
                key={donationType + donationPurpose}
                amount={amount}
                priceTag="USD"
                setAmount={setAmount}
              />
            )}
            <DonationUserInfo userInfo={userInfo} setUserInfo={setUserInfo} />
            <DonationFooter />
          </Fragment>
        )}
        {pageNumber === 1 && (
          <PaymentForm
            setStripeElementsComplete={setStripeElementsComplete}
            amountString={amountString}
            donationPurpose={donationPurpose}
            handleBackClick={handleBackClick}
            payerName={payerName}
            setPayerName={setPayerName}
            isLoading={stripeLoading || payPalLoading}
            paymentMethod={paymentMethod}
            setPaymentMethod={setPaymentMethod}
          />
        )}
        {pageNumber === 2 && <ThankYouBox handleBackClick={handleBackClick} />}
        {pageNumber === 3 && <ErrorBox handleBackClick={handleBackClick} />}
      </form>
      {(stripeLoading || payPalLoading) && <FixedLoader />}
    </Fragment>
  )
}

export default DonationBox
